import React from "react";

const Services = () => {
  // services details
  const services = [
    {
      name: "Documentación",
      desc: "Utilizo esta página para guardar mis notas y apuntes técnicos que utilizo en mi día a día. Puedes navegar a la sección de documentación desde el menú superior.",
      icon: "fas fa-book",
      link: "/docs"
    },
    {
      name: "Blog",
      desc: "Para contar mis experiencias y compartir mis conocimientos, he creado este blog. Puedes navegar a la sección de blog desde el menú superior.",
      icon: "fas fa-desktop",
      link: "/blog"
    },
    {
      name: "YouTube",
      desc: "Otra de mis pasiones es el mundo audiovisual. En mi canal de YouTube puedes encontrar mis vídeos. También subo algunos shorts a TikTok.",
      icon: "fab fa-youtube",
      link: "https://www.youtube.com/channel/UCGfKISiN7usAdxvcGivm_OA"
    },
    {
      name: "Twitter",
      desc: "Suelo compartir mis artículos y vídeos en Twitter. También puedes seguirme para estar al día de mis publicaciones.",
      icon: "fab fa-twitter",
      link: "https://twitter.com/pabpereza"
    },
    {
      name: "LinkedIn",
      desc: "En LinkedIn puedes encontrar mi perfil profesional, curriculum, certificaciones y trabajos trayectoria en general",
      icon: "fab fa-linkedin",
      link: "https://www.linkedin.com/in/pablo-pérez-aradros-calvo-516634109/"
    },
    {
      name: "TikTok",
      desc: "Intento crear contenido corto a partir de mis vídeos de YouTube. Puedes seguirme en TikTok para estar al día de mis publicaciones.",
      icon: "fab fa-tiktok",
      link: "https://www.tiktok.com/@pabpereza"
    },


  ];

  return (
    <section id="services" className="section bg-light">
      <div className="container">
        {/* Heading */}
        <p className=" text-center mb-2 wow fadeInUp">
          <span className="bg-primary text-dark px-2 escritura">Intento invertir parte de mi tiempo libre en crear contenido y seguir aprendiendo</span>
        </p>
        <h2 className="text-10 fw-600 text-center mb-5 wow fadeInUp">
          Aquí puedes encontrar mi contenido
        </h2>
        {/* Heading end*/}
        <div className="row gy-5 mt-5">
          {services.length > 0 &&
            services.map((service, index) => (
              <div className="col-sm-6 col-lg-4 wow fadeInUp" key={index}>
                
                <div className="featured-box text-center px-md-4">
                <a target="_blank"  href={service.link}>
                  <div className="featured-box-icon text-primary text-13">
                    {" "}
                    <i className={service.icon} />
                  </div>
                  <h3 className="text-6 fw-600 mb-3">{service.name}</h3>
                  </a>
                  <p className="text-muted mb-0">{service.desc} </p>
                </div>
              
              </div>
            ))}
        </div>
      </div>
    </section>
  );
};

export default Services;
